import React from 'react'
import './nav_styles.css'
import './main_styles.css'
export default function BrandMenu() {
 
  const HideBrands =()=>{
    for(var i = 0; i < document.getElementById("section-wrapper").children.length; i++){
      document.getElementById("section-wrapper").children[i].style.display = "none";
    }
  }

  const ShowClassic  =()=>{ 
    HideBrands();
   document.getElementById("classic-container").style.display = "flex";
   document.documentElement.style.setProperty("--main-color", "#03004F");
   document.documentElement.style.setProperty("--secondary-color", "#00003A");
  }

  const ShowBees  =()=>{ 
    HideBrands();
   document.getElementById("bee-container").style.display = "flex";
   document.documentElement.style.setProperty("--main-color", "#FFF100");
   document.documentElement.style.setProperty("--secondary-color", "black");
  }

  const ShowHoliday  =()=>{ 
    HideBrands();
   document.getElementById("holiday-container").style.display = "flex";
   document.documentElement.style.setProperty("--main-color", "#146B3A");
   document.documentElement.style.setProperty("--secondary-color", "#165B33");
  }

  return (
    <>
       <li>Brands
         <div id = "brand-menu">
           <button onClick = {ShowClassic} id = "classic-brand-btn">Good News Classic</button>
           <button onClick = {ShowBees} id = "bee-brand-btn">Save The Bees</button>
           <button onClick = {ShowHoliday} id = "holiday-brand-btn">Happy Holidays</button>
         </div>
       </li>
    </>
  )
}
