import React from 'react'
export default function Gallery({item, name, price, description}) {

  const highlight =()=>{
   document.querySelector("#highlight-image-container img").src = item;
   document.querySelector("#highlight-image-container img").alt = name;
   document.querySelector("#highlight-info-container h2").innerHTML = name;
   document.querySelector("#highlight-info-container h3").innerHTML = price;
   document.querySelector("#highlight-info-container p").innerHTML = description;
   document.querySelector("#quantity").innerHTML = 0;
   document.querySelector("#purchase-container").style.display = "block"
   window.scrollTo(1000,document.body.scrollHeight);
  }

  
  
  return (
    <li>
      <img onClick = {highlight} src = {item} alt = {name} title = {price}/>
      <span>{name}</span>
      <span>${price}</span>
      <p>{description}</p>
    </li>
  )
}
