import React from 'react'
import Nav from './Nav';
export default function Brands({logo, title, about, banner, product1, product2, product3}) {

  return (
    <>
    <div className = "left-section">
      <Nav/>
      <img className = "logo" alt ="Good News Logo" src = {logo}/>
    </div>
    
    <div className = "right-section">
      <header>
        <img src = {banner} alt="dan"/>
        <h1>{title}</h1>
        <p>{about}</p>
      </header>

      <div id = "featured-products">
        <img src = {product1} alt = "Featured Product" />
        <img src = {product2} alt = "Featured Product" />
        <img src = {product3} alt = "Featured Product" />
      </div>
    </div>
    </>
  )
}
