import React , {useState} from 'react'
import BrandMenu from './BrandMenu.js'
import './nav_styles.css'

export default function Nav(){  
  
  const [isDown, setDown] = useState(false);
  const dropDown =()=>{
    setDown(!isDown);
  }

  const revealShop =()=>{
    document.querySelector("#shop-wrapper").style.display = "flex";
    window.scrollTo(1000,document.body.scrollHeight);
  }

  return(
    <nav>
      <button onClick = {dropDown} id ="burger"><img src = "images/hamburger-menu.png" alt ="hamburger menu"/></button>
     <ul className = {isDown ? 'down' : ''}>
        <BrandMenu />
        <li onClick = {revealShop}>Shops</li>
        <li><a href = "http://justinhayesdouglas.com/">Contact</a></li>  
      </ul>
   </nav>
   
  )
}

