import React from 'react';


export default function QuantityBtns() {
  
 const increment =()=>{
   var itemQuantity = document.getElementById("quantity").innerHTML
   var itemPrice = document.querySelector("#highlight-info-container h3").innerHTML;
   var subtotal = itemPrice*(parseInt(itemQuantity) + 1);
  
   itemQuantity++
   sessionStorage.setItem("itemSubtotal", subtotal);
   document.getElementById("quantity").innerHTML = itemQuantity;
 }

 const decrement =()=>{
   var itemQuantity = document.getElementById("quantity").innerHTML;
   var itemPrice = document.querySelector("#highlight-info-container h3").innerHTML;
   var subtotal = itemPrice*(parseInt(itemQuantity - 1));
  
   itemQuantity--
   sessionStorage.setItem("itemSubtotal", subtotal);
   document.getElementById("quantity").innerHTML = itemQuantity;

   if(itemQuantity < 0){
    document.getElementById("quantity").innerHTML = 0;
  }
 }
 
  return (
    <>
    <div id = "quantity-btns-container">
      <button onClick={increment}>+</button>
      <span id = "quantity">0</span> 
      <button onClick={decrement}>-</button>
    </div>
    </>
  )
}
